<template>
  <div class="page-view no-header">
    <div class="video-box">
      <navbar class="video-header" :bgcolor="'rgba(0,0,0,0)'" :nobg="true"
              :title="video.title"></navbar>
      <iframe width="100%" :src="video.video_address" frameborder="0"></iframe>
    </div>
    <yd-tab class="video-tab">
      <yd-tab-panel label="列表">
        <h4 class="pd-15">正在播放：</h4>
        <video-list :courses="videolist"></video-list>
      </yd-tab-panel>
      <yd-tab-panel label="笔记">
        <note></note>
      </yd-tab-panel>
      <yd-tab-panel label="附加视频">
       <!-- <list></list>-->
      </yd-tab-panel>
    </yd-tab>
    <yd-cell-group>
      <yd-cell-item arrow type="a" href="#/detail">
        <span slot="left">系列课:如何打造机器人</span>
      </yd-cell-item>
    </yd-cell-group>
  </div>
</template>

<script type="text/ecmascript-6">
  import VideoList from './templates/CouseList3.vue'
  import Note from './templates/NoteList.vue'
  export default{
    components: {
      VideoList,
      Note
    },
    data () {
      return {
        index: 0,
        video:{},
        videolist:[]
      }
    },
    computed: {
      view () {
        return this.index ? 'note' : 'list'
      }
    },
    created(){
      this.getData()
    },
    methods:{
      getData(){
        var self=this
        this.$api.video({cid:this.$route.params.id}).then(function (res) {
          if(res.data&&res.data.code==1){
            self.videolist=res.data.msg
            if(self.videolist.length>0){
              self.video=self.videolist[0]
            }
          }
        })
      }
    }

  }
</script>

<style lang="less" rel="stylesheet/less">
  @import "../../style/config.less";
  video{
    width: 100%;
  }
  .video-header{
    .navbar-item{
      flex:0 0 0;
    }
    &> :first-child{
      flex-basis: 60px;
    }
    .navbar-center:nth-child(2){
      flex:1;
      justify-content: flex-start;
    }
    .navbar-item:nth-child(3){
      display: none;
    }
  }
  .video-tab{
    .tab-nav-item.tab-active{
      color:@skin-color;
    }
  }
</style>



